import Login from "./client/components/Login/Login";
import React, { useState, useEffect } from 'react';

import './App.css';

function App() {

   const [serverMessage, setServerMessage] = useState('Loading...');

  useEffect(() => {
    // Fetch data from your Express API endpoint
    fetch('/api/message') // In development, this automatically proxies to http://localhost:8080/api/message
      .then(response => {
        // Check if the response is successful
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        return response.json();
      })
      .then(data => {
        // Store the message from the server's JSON response
        setServerMessage(data.message);
        console.log('API call successful!'); // Logs to the BROWSER console
      })
      .catch(error => {
        console.error("Fetch error:", error);
        setServerMessage('Failed to connect to API.');
      });
  }, []); // The empty array [] ensur
  return (
    <div className="App">
     <h1>Frontend Status</h1>
      <p>Message from server: <strong>{serverMessage}</strong></p>
      <Login/>
    </div>
  );
}

export default App;
